import 'rc-time-picker/assets/index.css';

import React from 'react'
import moment from 'moment';
import Moment from 'react-moment';
import TimePicker from 'rc-time-picker';

import Layout from '../components/layout'

class IndexPage extends React.Component {
  static displayName = 'TestComponent';

  state = { time: new Date() };
  
  render() {
    const showSecond = false;

    return <div>
      <Layout>
        <div style={{marginBottom: 30 + 'px'}}>
          <h3>Chọn giờ bay</h3>
          <TimePicker
            style={{ width: 250 }}
            showSecond={showSecond}
            defaultValue={moment()}
            className="xxx"
            onChange={(value) => this.setState({ time: value && value.format('YYYY-MM-DD HH:mm:ss') })}
          />
        </div>

        <table style={{width: 100 + '%'}}>
          <tbody>
            <tr>
              <th>Chặng</th>
              <th>Giờ briefing</th>
              <th>Ghi chú</th>
            </tr>
            <tr>
              <td>Nội địa</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 20 }}>{this.state.time}</Moment></td>
              <td>Có mặt trước 1h20</td>
            </tr>
            <tr>
              <td>Ngắn quốc tế</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 35 }}>{this.state.time}</Moment></td>
              <td>Có mặt trước 1h35</td>
            </tr>
            <tr>
              <td>Dài quốc tế</td>
              <td><Moment format="HH:mm" subtract={{ hours: 2, minutes: 5 }}>{this.state.time}</Moment></td>
              <td>Có mặt trước 2h05</td>
            </tr>
          </tbody>
        </table>
      </Layout>
    </div>
  }
}


export default IndexPage
